.postFormModal.active {
  display: block;
}

.postFormModal.hidden {
  display: none;
}

.postForm.hidden {
  display: hidden;
}

.postForm.active {
  display: block;
}

.postForm.none {
  display: none;
}

.postFormModal {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  background-color: var(--tumblr-deep-blue-modal);
  z-index: 100;

  .postFormContainer {
    position: absolute;
    top: 10%;
    width: 540px;
    min-height: 150px;
    left: 50%;
    border: 1px solid black;
    background-color: white;
    border-radius: 5px;
    transform: translateX(-50%) scale(1);

    @media (max-width: 980px) {
      width: 380px;
      min-height: 200px;

      @media (max-width: 375px) {
        width: 350px;
      }

      @media (max-width: 320px) {
        top: 10%;
        width: 385px;
        transform: translateX(-50%) scale(.8);
      }

      @media (max-width: 280px) {
        top: 10%;
        width: 320px;
      }
    }


    .profilePic {
      display: block;
      position: absolute !important;
      top: 0px !important;
      left: -88px !important;
      z-index: 103 !important;

      @media (max-width: 980px) {
        display: none;
      }
    }

    .postForm {
      position: relative;
      box-sizing: border-box;
      z-index: 102;

      h3 {
        font-family: var(--font);
        font-size: 14px;
        font-weight: 700;
        margin: 15px 0 15px 16px;
      }

      .mainPreview, .bodyPreview {
        
        .imageContentContainer {
          position: relative;
          height: fit-content;
          width: 100%;
          z-index: 101;

          &:focus {
            .removeBtn {
              transform: scale(1);
            }
          }

          &:hover {
            .removeBtn {
              transform: scale(1);
              cursor: pointer;
            }
          }
          
          .removeBtn {
            position: relative;
            top: 8px;
            left: 530px;
            left: 98%;
            height: 20px;
            width: 20px;
            font-weight: bold;
            border-radius: 50%;
            background-color: red;
            color: white;
            transform: scale(0);
            transition: transform .2s .5s;

            @media (max-width: 980px) {
              left: 93%;
            }

            span {
              position: relative;
              display: inline-block;
              top: -1.5px;
              left: .5px;
              font-size: 15px;
            }
          }

          .imageContent {
            width: 100%;
            object-fit: contain;
          }
        }
      }

      .textContentContainer {
        position: relative;
        left: 10px;
        margin: 10px 0 -8% 0;
        width: 510px;
        z-index: 100;

        @media (max-width: 980px) {
          width: 350px;

          @media (max-width: 375px) {
            width: 330px;
          }

          @media (max-width: 320px) {
            width: 350px;
          }

          @media (max-width: 280px) {
            width: 300px;
          }
        }

        .ck {
          width: 100%;
          height: fit-content;
        }
      }

      .bodyImageInputContainer {
        position: absolute;
        display: flex;
        width: 500px;
        height: 200px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 500;
        box-sizing: border-box;
        border: 3px dashed rgb(0, 0, 0, 0.3);
        background-color: var(--less-light-gray);

        @media (max-width: 980px) {
          width: 365px;

          @media (max-width: 375px) {
            width: 330px;
          }

          @media (max-width: 320px) {
            width: 370px;
          }
  
          @media (max-width: 280px) {
            width: 300px;
          }
        }

  
        textarea {
          position: relative;
          top: -110px;
          left: 320px;
          font-size: 17px;
          font-weight: 500;
          font-family: var(--font);

          @media (max-width: 980px) {
            top: -111px;
            left: 228px;

            @media (max-width: 375px) {
              left: 200px;
            }

            @media (max-width: 320px) {
              left: 220px;
            }
  
            @media (max-width: 280px) {
              left: 180px;
            }
          }
        }
  
        .backBtn {
          position: relative;
          top: -5px;
          left: -2px;
          width: 50px;
          height: 50px;
        }
  
        .bodyImageInputCustomLabel {
          border-right: 3px dashed rgb(0, 0, 0, 0.3);
          width: 250px;
          height: 200px;

          @media (max-width: 980px) {
            width: 180px;

            @media (max-width: 375px) {
              width: 162px;
            }

            @media (max-width: 320px) {
              width: 180px;
            }
  
            @media (max-width: 280px) {
              width: 148px;
            }
          }

          span {
            display: inline-block;
            position: relative;
            font-weight: 500;
            top: 90px;
            left: 50%;
            transform: translateX(-50%);
            color: rgb(0, 0, 0, 0.5)
          }
  
          &:hover {
            cursor: pointer;
          }
  
          #bodyFileInput {
            display: none;
          }
        }
      }
  
  
      .photoInputIcon {
        display: block;
        position: relative;
        top: 5px;
        left: 90%;
        width: 23px;        
        height: 23px;
        filter: grayscale(100%);
        z-index: 500;
        
        &:hover {
          cursor: pointer;
        }
      }

      .descriptionStringContainer {
        margin-top: -15px;

        .ck {
          position: relative;
          width: 500px;
          
          @media (max-width: 980px) {
            width: 80%;
          }
  
          left: 12px;
          min-height: 110px;
          z-index: 10;
          
          &:focus {
            outline: none;
            border: none;
            box-shadow: none;
          }
        }
      }
    }
  }

      .tagsContainer {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        width: 100%;
        height: auto;
        white-space: normal;
        margin: 0 0 0 0;
        padding: 0 0 20px 0;
        border-bottom: 1px solid rgb(0, 0, 0, 0.1);
      
        .tagDiv {
          display: flex;
          font-size: 15px;
          margin: 0 3px 8px 3px;
          padding: 0 10px 3px 5px;
          height: fit-content;
          border-radius: 15px;
          background-color: var(--tumblr-lighter-blue);
          color: white;
          float: left;
          
          &:hover {
            cursor: default;
          }
        
          .removeTag {
            position: relative;
            display: block;
            top: 6px;
            left: 4px;
            height: 13px;
            width: 10px;
            text-align: center;
            border-radius: 50%;
            margin-left: 3px;
          
            .removeTag {
              display: inline-block;
            }
          
            span {
              display: block;
              position: relative;
              top: -5.3px;
              font-weight: bold;
              font-size: 13px;
            }
          }
        }
    
        .tagInputAndDDContainer {
          display: inline-block;
          position: relative;
          width: fit-content;
          
          input {
            margin: -2px 0 0 15px;
            font-size: 17px;
          }
  
          .matchingTagsDD.active {
            display: block;
          }

          .matchingTagsDD {
            display: none;
            position: absolute;
            top: 30px;
            left: -40px;
            min-width: 190px;
            background-color: white;
            border-radius: 5px;
            box-sizing: border-box;
            border: 1px solid var(--light-gray);
            z-index: 97;
            
            li {
              padding: 5px 0 5px 10px;

              &:not(:first-child) {
                &:hover {
                  background-color: var(--tag-hover-blue);
                }
              }

              &:first-child {
                font-size: 12px;
                font-weight: 500;
                padding: 3px 0 5px 5px;
                color: rgb(0, 0, 0, 0.3);
                background-color: var(--less-light-gray);

                &:hover {
                  cursor: default;
                }
              }

              &:hover {
                cursor: pointer;
              }
            }
          }          
        }
      }

      .closeOrPostContainer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 65px;

        .closeBtn {
          position: relative;
          background-color: rgb(0, 0, 0, 0.4);
          height: fit-content;
          width: fit-content;
          font-size: 14px;
          font-weight: 700;
          padding: 6px 10px 8px 10px;
          margin-left: 17px;
          border-radius: 3px;
          color: white;

          &:hover {
            cursor: pointer;
          }
        }

        .confirmCloseModal {
          position: fixed;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          background-color: var(--tumblr-deep-blue-modal);
          z-index: 500;
        }

        .confirmCloseContainer {
          position: absolute;
          color: white;
          top: 40%;
          left: 50%;
          transform: translateX(-50%);
          width: 280px;
          z-index: 501;

          .discardTitle {
            display: inline-block;
            margin: 0 0 15px 42px;
            font-size: 24px;

            &:hover {
              cursor: default;
            }
          }

          div {
            position: relative;
            display: flex;
            justify-content: space-between;

            .cancel {
              position: relative;
              padding: 10px 25px 10px 25px;
              border-radius: 2px;
              font-weight: bold;
              background-color: var(--best-gray);
              
              &:hover {
                cursor: pointer;
              }
            }
            
            .discard {
              position: relative;
              padding: 10px 38px 10px 38px;
              font-weight: bold;
              border-radius: 2px;
              background-color: var(--tumblr-light-blue);

              &:hover {
                cursor: pointer;
              }
            }
          }
        }

        .formSubmitBtn {
          position: relative;
          display: block;
          font-size: 15px;
          font-weight: 700;
          height: fit-content;
          width: fit-content;
          margin-right: 17px;
          padding: 8px 14px 8px 14px;
          border-radius: 3px;
          background-color: var(--tumblr-light-blue);
          color: white;

          &:hover {
            cursor: pointer;
          }
        }

        .formSubmitBtn.disabled {
          color: rgb(255, 255, 255, .5)
        }
      }
      
      .textPostForm {

        .titleInput {
          display: block;
          position: relative;
          left: 15px;
          width: 510px;
          font-size: 34px;
        }
        
        .titleInput.placeholder:empty:before {
          content: attr(data-placeholder);
          color: rgb(0, 0, 0, 0.5);
        }
        
        .titleInput.active {
          font-size: 34px;

          &:focus {
            outline: none;
            border: none;
          }
      
          &::placeholder {
            color: rgb(0, 0, 0, 0.7);
          }
       }
    }


