            transform: scale(.8);
  
            @media (max-width: 420px) {
              left: 196px;
            }
  
            @media (max-width: 375px) {
              left: 180px;
            }
  
            @media (max-width: 280px) {
              left: 165px;
            }
          }
        }

        .linkContainer {
          position: relative;
          transform: scale(1);
          left: 45px;

          @media (max-width: 980px) {
            left: -15px;
            transform: scale(.8);

            @media (max-width: 420px) {
              left: -13px;
            }
  
            @media (max-width: 375px) {
              left: 0px;
            }
  
            @media (max-width: 280px) {
              left: 8px;
            }
          }

          .linkIcon {
            position: absolute;
            filter: grayscale(100%);
            top: 55px !important;
            left: 80px;
          }
    
          textarea {
            position: relative;
            top: 131px;
            left: 68px;
            font-size: 17px;
            font-weight: 500;
            font-family: var(--font);
          }
        }

  
        .videoFileInputCustomLabel {
          position: relative;
          width: 250px;
          height: 200px;
          transform: scale(1);

          @media (max-width: 980px) {
            left: -5px;
            transform: scale(.8);
  
            @media (max-width: 420px) {
              left: -5px;
            }
  
            @media (max-width: 280px) {
              left: 5px;
            }
          }

          div {
            display: flex;
            position: relative;
            flex-direction: column;
            top: 95px;
            left: 5px;
            transform: translateY(-50%);

            .videoIcon {
              position: absolute;
              filter: grayscale(100%);
              top: -42px !important;
              left: 82px;
              width: 85px;

              @media (max-width: 980px) {
                left: 52px;
                transform: scale(.8);
      
                @media (max-width: 420px) {
                  left: 56px;
                }
      
                @media (max-width: 375px) {
                  left: 40px;
                }
      
                @media (max-width: 280px) {
                  left: 30px;
                }
              }
            }
        
            span {
              position: relative;
              display: block;
              margin: auto;
              width: fit-content;
              top: 44px;
              font-size: 15px;
              font-weight: 500;
              color: rgb(0, 0, 0, 0.5);
            }
          }
  
          &:hover {
            cursor: pointer;
          }
  
          #videoFileInput {
            display: none;
          }
        }
      }
    }
  }
